// @ts-ignore
import sha1 from 'sha-1';
import { postRequest } from './util/requestAlias';
import { buildURL, buildRequestBody } from './util/requestHandler';
import { TokenResponse } from './models';
import defaults from './defaults';

interface LoginParams {
  /**
   * ç”¨æˆ·åæˆ–é‚®ç®±
   */
  name: string;
  /**
   * ç»è¿‡ hash1 åŠ å¯†åçš„å¯†ç ã€‚
   *
   * * è‹¥ä½ ä½¿ç”¨çš„æ˜¯ [mdclub-sdk-js](https://github.com/zdhxiong/mdclub-sdk-js)ï¼Œåˆ™ SDK å†…éƒ¨å·²ç»å®ç°äº† sha1 åŠ å¯†ï¼Œè¯¥å‚æ•°ç›´æ¥ä¼ å…¥æ˜æ–‡å¯†ç å³å¯ã€‚
   * * è‹¥ä½ ç›´æ¥è°ƒç”¨ REST APIï¼Œåˆ™éœ€è¦è‡ªå·±å®ç° sha1 åŠ å¯†ã€‚
   */
  password: string;
  /**
   * è®¾å¤‡ä¿¡æ¯
   */
  device?: string;
  /**
   * å›¾å½¢éªŒè¯ç tokenã€‚è‹¥ä¸Šä¸€æ¬¡è¯·æ±‚è¿”å›äº† `captcha_token`ï¼Œ åˆ™å¿…é¡»ä¼ è¯¥å‚æ•°
   */
  captcha_token?: string;
  /**
   * å›¾å½¢éªŒè¯ç çš„å€¼ã€‚è‹¥ä¸Šä¸€æ¬¡è¯·æ±‚è¿”å›äº† `captcha_token`ï¼Œåˆ™å¿…é¡»ä¼ è¯¥å‚æ•°
   */
  captcha_code?: string;
}

/**
 * ç”Ÿæˆ Token
 *
 * é€šè¿‡è´¦å·å¯†ç ç™»é™†ï¼Œè¿”å› Token ä¿¡æ¯ã€‚  è‹¥ç™»å½•å¤±è´¥ï¼Œä¸”è¿”å›ä¿¡æ¯ä¸­å«å‚æ•° &#x60;captcha_token&#x60; å’Œ &#x60;captcha_image&#x60;ï¼Œ è¡¨ç¤ºä¸‹æ¬¡è°ƒç”¨è¯¥æ¥å£æ—¶ï¼Œéœ€è¦ç”¨æˆ·è¾“å…¥å›¾å½¢éªŒè¯ç ï¼Œå¹¶æŠŠ &#x60;captcha_token&#x60; å’Œ &#x60;captcha_code&#x60; å‚æ•°ä¼ é€’åˆ°æœåŠ¡ç«¯ã€‚
 */
export const login = (params: LoginParams): Promise<TokenResponse> => {
  if (params.password) {
    params.password = sha1(params.password);
  }

  return postRequest(
    buildURL('/tokens', params),
    buildRequestBody(params, [
      'name',
      'password',
      'device',
      'captcha_token',
      'captcha_code',
    ]),
  ).then((response) => {
    if (!response.code) {
      defaults.adapter!.setStorage(
        'token',
        (response as TokenResponse).data.token,
      );
    }

    return response;
  });
};
